import { Box, Grommet, Text } from "grommet";
import React from "react";

const theme = {
    global: {
      font: {
        family: 'Prosto One',
      },
    },
    card: {
      container: {
        background: '#1a3a39',
        elevation: 'none',
      },
      footer: {
        pad: { horizontal: 'medium', vertical: 'small' },
      },
    },
    heading: {
      color: '#ADFF0037',
      level: {
        2: {
          medium: {
            size: "30px",
            height: "40px",
          }
        }
      }
    },
  };

export type MessageProps = {
    id: any;
    title: string;
    createdAt: string;
    authorUserName: string;
    // deleteMessage: () => void
    // editMessage: (newTitle: string) => void
  };

  export const MessageComponent: (props: MessageProps) => JSX.Element = (
    props: MessageProps
  ) => {
      return(<Grommet theme={theme}>
          <Box margin="medium">
              <Text size="large" margin="small" color="#ADFF00">{props.authorUserName}</Text>
              <Text size="small">{props.title}</Text>
          </Box>
      </Grommet>)
  }